@mixin title {
    font-size: 35px;
    font-family: $font-title;
    text-align: center;
    color: $color-secondary-dark;
    text-transform: uppercase;
    margin-bottom: 60px; }

@mixin btn {
    color: $color-primary-dark;
    font-family: $font-title;

    padding: 15px 55px;

    transition: background-color .1s linear, color .1s linear;

    border: 1px $color-secondary-dark solid;
    border-radius: 100px;

    background-color: $color-secondary-dark;

    &:hover {
        transition: background-color .1s linear, color .1s linear;
        background-color: #f58d2b;
        border-color: #f58d2b;
        color: $color-secondary-light; } }

@mixin img-filter {
    display: block;
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    opacity: .4;
    background-color: #3d3d3d; }

